<template>
    <div class="eq-info">
        <h3> Earthquake data </h3>
        <p ref="info"> <b> Click on a circle to get more info on the earthquake! </b></p>
        <ul style="list-style: none;">
            <li v-for ='(key, value) in getCurrData' :key="value">
                <b>{{ value }}:</b> {{ key }}
            </li>
        </ul>
        <br />
        <p> Find a description of the earthquake parameters <a href="https://earthquake.usgs.gov/data/comcat/index.php">here.</a></p>

    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    export default {
        name: 'eqInfo',
        components: {
            mapGetters,
        },
        data() {
            return {
                data_clean: {}
            }
        },
        updated () {
            this.cleanData();
        },
 
        computed: {
            ...mapGetters(['getCurrData']),

        },
        methods: {
            cleanData() {
                this.data_clean = this.getCurrData;  
                this.data_clean.time = Date(this.getCurrData.time);  // turn string format to date format
                this.data_clean.updated = Date(this.getCurrData.updated);
            }  
        },
    }
</script>

<style scoped>
h3 {
    text-align: center;
}
.eq-info {
    border: 2px solid rgb(129, 87, 117);
    border-radius: 5px;
    overflow-y: auto;
}
p {
    margin: 5px
}
li {
    font-size: 12px;
}
#info {
    text-align: center;
}

</style>